// TODO: PartialEq, Congruence 

/**
 *  [[equals]]  [[Eq]]  [[canEqual]] 
 *
 *  [[Eq]]  [[equals]]  [[Eq]]  a, b, c 
 *
 * - a.equals(a) 
 * - a.equals(b)  b.equals(a) 
 * - a.equals(b)  b.equals(c)  a.equals(c) 
 *
 * [[Eq]]  [[Eq]]  [[canEqual]]  [[Eq]]  [[Eq]]  A, B, C  a, b, c 
 *
 * - a.canEqual(a) 
 * - a.canEqual(b)  b.canEqual(a) 
 * - a.canEqual(b)  b.canEqual(c)  a.canEqual(c) 
 *
 * [[Eq]]  [[Eq]]  A, B  a.canEqual(b)  A-B  [[Eq]]  [[Eq]]  A, B  a, b  a.equals(b)  a.canEqual(b) 
 *
 * @implNote [[equals]]  [[canEqual]] 
 */
export default abstract class Eq implements Class.Eq {
  /**
   * {this}  {that} 
   *
   * @implNote  {that} 
   */
  protected abstract canEqual(that: Eq): boolean

  /**
   * {this}  {that} 
   *
   * @implNote  `this.canEqual(that) && that.canEqual(this)`  FALSE 
   */
  public abstract equals(that: Eq): boolean

  /**
   * @final
   */
  public isNotEqualTo(that: Eq): boolean {
    return !this.equals(that)
  }
}
